html,
body {
    height: 100%;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f1faee; /* Light background color */
}

.game {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.board {
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-template-rows: repeat(3, 100px);
    gap: 5px;
    background-color: #a8dadc; /* Light blue background for the board */
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.cell-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.cell {
    width: 100px;
    height: 100px;
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: #457b9d; /* Medium blue background for cells */
    color: #f1faee; /* Light color for text */
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s;
}

.cell:hover {
    background-color: #1d3557; /* Dark blue background on hover */
}

.highlight {
    background-color: #e63946; /* Red background for highlighted cells */
    color: #f1faee; /* Light color for text */
}

.winner {
    margin-top: 20px;
    font-size: 1.5rem;
    color: #1d3557; /* Dark blue color for winner text */
}

.reset {
    margin-top: 10px;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    background-color: #457b9d; /* Medium blue background for button */
    color: #f1faee; /* Light color for button text */
    border: none;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s;
}

.reset:hover {
    background-color: #1d3557; /* Dark blue background on hover */
}
